.vote-button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: transparent;
  border: none;
  padding: 0;
  font-family: "Gillsans", "Roboto", Helvetica, sans-serif;
  cursor: pointer;
  border-radius: 0;
  padding: 8px;
  border: 1px solid #e6ecf0;
  border-radius: 3px;
  background-color: rgba(113, 129, 255, 0);
  -webkit-transition: background-color 125ms ease, border-color 125ms ease;
  -o-transition: background-color 125ms ease, border-color 125ms ease;
  transition: background-color 125ms ease, border-color 125ms ease; }
  .vote-button .thumbs-up path {
    -webkit-transition: stroke 125ms ease;
    -o-transition: stroke 125ms ease;
    transition: stroke 125ms ease; }
  .vote-button.is-active, .vote-button:hover {
    background-color: #7181ff;
    border-color: #7181ff; }
    .vote-button.is-active .thumbs-up path, .vote-button:hover .thumbs-up path {
      stroke: white; }
  .vote-button.is-active:hover {
    background-color: rgba(113, 129, 255, 0);
    border-color: #e6ecf0; }
    .vote-button.is-active:hover .thumbs-up path {
      stroke: #666; }
  .vote-button div {
    width: 16px;
    height: 16px; }



